import React, { Component } from 'react';
import {
  Col,
  Row,
} from 'reactstrap';
import SlickCarousel from '../SlickCarousel';
import mainImg from 'assets/images/Rewards/main.svg';
import Logos from 'assets/images/InsurerLogos';
import './Welcome.scss';

class Welcome extends Component {
  render() {
    return (
      <Col xs="12">
        <div className="welcome">
          <Row className="align-items-center" noGutters>
            <Col xs="12" md="6" className="order-md-2 mb-5 mb-md-0">
              <img src={mainImg} className="welcome__main-img" alt="PolicyPal Rewards" />
            </Col>

            <Col xs="12" md="6" className="order-md-1">
              <h1 className="welcome__title text-center text-md-left">Welcome to PolicyPal Rewards!</h1>
              <p className="welcome__sub-title text-center text-md-left">Where you can do more and redeem awesome rewards with your PolicyPal Credits</p>

              <p className="welcome__partner-title">Our Partners</p>
              
              <SlickCarousel
                variableWidth={false}
                autoplay={false}
                infinite
                images={Array.from(Object.keys(Logos.smallTinted), o => Logos.smallTinted[o])}
              />
            </Col>
          </Row>
        </div>
      </Col>
    )
  }
}

export default Welcome;
