import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';
import InfoPage from 'components/Templates/InfoPage/InfoPage';
import TnC from 'components/Templates/TnC/TnC';
import Button from 'components/FormsV3/Button/Button';
import SlickCarousel from './SlickCarousel';
import Welcome from './Welcome';
import Mouse from './Mouse';
import Title from './Title';
import Guides from './Guides';
import {
  HOW_TO_EARN,
  TNC,
} from './Rewards.content';
import termImg from 'assets/images/Rewards/term.svg';
// import wtcImg from 'assets/images/Rewards/where__both.png';
import './Rewards.scss';

class Rewards extends Component {
  componentWillMount() {
    const { getBlogRewardData } = this.props;
    getBlogRewardData();
  }
  componentDidMount() {
    window.scrollTo(0, 0);
  }

  getTnCContent() {
    const contents = [];

    for(let i = 0; i < TNC.length; i++) {
      const { title, details } = TNC[i];
      
      const obj = (
        <div key={`tnc-${i}`} className="term-block">
          <h5 className="title">{title}</h5>
          <ol className="details-list">
            { details.map((v, idx) => (<li className="detail" key={`${i}-${idx}`}>{v}</li>)) }
          </ol>
        </div>
      );

      contents.push(obj);
    }

    return {data: { contents }};
  }

  renderBlog(data, index) {
    const { title, image, link, created_date } = data;
    return (
      <div
        key={`blog-${index}`}
        className="item"
        onClick={() => window.open(link, '_blank')}
      >
        <div className="image" style={{backgroundImage: `url(${image})`}} />
        <h5 className="title" dangerouslySetInnerHTML={{__html: title || '&nbsp;'}}/>
        <p className="desc">Date: {created_date}</p>
      </div>
    );
  }

  renderCredit(data, index) {
    return (
      <div key={`credit-${index}`} className="item">
        <div className="image">
          <img src={data.icon} alt={`${data.title}.svg`} />
        </div>
        <h5 className="title">{data.title}</h5>
        <p className="desc">{data.desc}</p>
      </div>
    );
  }

  render() {
    const { blogs } = this.props;

    return (
      <InfoPage
        className="rewards-container info-page-margin"
        noPolicyPal
        subscribeOverwrite={<Fragment />}
        noAutoMargin
        mode="full"
      >
        <Welcome />

        <Col xs="12">
          <Mouse className="d-none d-md-block ml-auto mr-auto mt-2 mb-4" />
        </Col>

        <Col xs="12">
          <Title
            backgroundText="PolicyPal Credits"
            foregroundText="How to earn PolicyPal Rewards"
          />
          <div className="credits">
            <SlickCarousel
              responsive={[
                {
                  breakpoint: 767,
                  settings: {
                    arrows: true,
                    slidesToShow: 2,
                  },
                },
                {
                  breakpoint: 575,
                  settings: {
                    arrows: true,
                    slidesToShow: 1,
                  },
                },
              ]}
            >
              { HOW_TO_EARN.map(this.renderCredit) }
            </SlickCarousel>
          </div>
        </Col>

        <Col xs="12">
          <div className="text-center mt-3 mb-3">
            <Button
              variant="medium-flat-green"
              height={48}
              width={162}
              onClick={() => document.querySelector('#our-blog').scrollIntoView({behavior: 'smooth'})}
            >
              <p>Learn More</p>
            </Button>
          </div>
        </Col>

        <Col xs="12">
          <Title
            backgroundText="GUIDE"
            foregroundText="How to use"
          />
          <Guides />
        </Col>

        {/* <Col xs="12">
          <Title
            backgroundText="WHERE"
            foregroundText="Where to check your PolicyPal Rewards?"
          />
          <Row noGutters className="where-to-check align-items-center align-items-md-end">
            <Col xs="12" sm="6" md="8">
              <img src={wtcImg} className="image" alt="Where to check your PolicyPal Rewards?" />
            </Col>
            <Col xs="12" sm="6" md="4">
              <div className="info">
                <h5 className="title">Website</h5>
                <p className="desc">Go to your ‘Profile’ and click on ‘Cashback’ tab to view your cashback overview and transaction history.</p>

                <h5 className="title">App</h5>
                <p className="desc">Tap on the ‘Briefcase’ icon on the App. Swipe right to view the cashback overview and transaction history.</p>
              </div>
            </Col>
          </Row>
        </Col> */}

        <Col id="our-blog" xs="12" className={(!blogs || blogs.length <= 0) ? 'd-none' : ''}>
          <Title
            backgroundText="MORE INFO"
            foregroundText="Our Blog"
          />
          <div className="blogs mb-5">
            <SlickCarousel
              responsive={[
                {
                  breakpoint: 767,
                  settings: {
                    arrows: true,
                    slidesToShow: 2,
                  },
                },
                {
                  breakpoint: 575,
                  settings: {
                    arrows: true,
                    slidesToShow: 1,
                  },
                },
              ]}
            >
              { blogs.map(this.renderBlog) }
            </SlickCarousel>
          </div>
          
        </Col>
        
        <Col xs="12">
          <Row className='tnc' noGutters>
            <Col xs="12" className="text-center">
              <img src={termImg} className="image" alt="PolicyPal" />
            </Col>
            <Col xs="12">
              <TnC data={this.getTnCContent()} />
            </Col>
          </Row>
        </Col>
      </InfoPage>
    );
  }
}

Rewards.defaultProps = {
  blogs: [],
};

Rewards.propTypes = {
  blogs: PropTypes.array,
};

export default Rewards;
