import React, { Component } from 'react';
import {
  Row,
  Col,
  Carousel,
  CarouselItem,
} from 'reactstrap';
import giftBoxImg from 'assets/images/Rewards/guide__gift-box.png';
import cashbackImg from 'assets/images/Rewards/guide__offset__both.png';
import rmStep1Img from 'assets/images/Rewards/guide__marketplace__step1.png';
import rmStep2Img from 'assets/images/Rewards/guide__marketplace__step2.png';
import rmStep3Img from 'assets/images/Rewards/guide__marketplace__step3.png';
import './Guides.scss';

const ITEMS = [
  {
    key: 'i-1',
    text: 'Offset your insurance policies',
  },
  {
    key: 'i-2',
    text: 'Rewards Marketplace',
  },
];

class Guides extends Component {
  constructor(props) {
    super(props);

    this.state = {
      activeIndex: 1
    };
    //
    this.animating = false;
    //
    this.goToIndex = this.goToIndex.bind(this);
    this.getCarouselItemAttr = this.getCarouselItemAttr.bind(this);
    this.renderIndicator = this.renderIndicator.bind(this);
  }
  
  goToIndex(newIndex) {
    const { animating } = this;
    if (!animating) {
      this.setState({ activeIndex: newIndex });
    }
  }

  getCarouselItemAttr() {
    return {
      onExiting: () => this.animating = true,
      onExited: () => this.animating = false,
    };
  }

  renderIndicator(item, index) {
    const { activeIndex } = this.state;
    const classes = ['indicator'];

    if (index === activeIndex) {
      classes.push('active');
    }

    return (<button
      type="button"
      key={`indicator-${index}`}
      className={classes.join(' ')}
      onClick={() => this.goToIndex(index)}
    >
      {item.text}
    </button>);
  }

  render() {
    const { activeIndex } = this.state;

    return (
      <Row className="guides align-items-center">
        <Col xs="12" md="3" lg="4">
          <div className="indicators">
            {ITEMS.map(this.renderIndicator)}
          </div>
          <img src={giftBoxImg} className="gift-box-image" alt="Guides" />
        </Col>
        <Col>
          <Carousel
            interval={false}
            activeIndex={activeIndex}
            next={() => this.next()}
            previous={() => this.previous()}
          >
            <CarouselItem key="offset-insurance-policies" {...this.getCarouselItemAttr()}>
              <div className="offset-insurance-policies">
                <img src={cashbackImg} alt="cashback guide" />
                <Row className="w-100" noGutters>
                  <Col xs="12" sm="6">
                    <p>At the checkout page, you can select your cashback as a payment option. The maximum cashback amount could be applied to offset the total cost.</p>
                  </Col>
                  <Col xs="12" sm="6">
                    <p>After you have successfully checked out with your rewards, you will see your rewards transaction history under your profile.</p>
                  </Col>
                </Row>
              </div>
            </CarouselItem>

            <CarouselItem key="rewards-marketplace" {...this.getCarouselItemAttr()}>
              <div className="rewards-marketplace">
                <Row className="w-100" noGutters>
                  <Col xs="12" sm="4">
                    <div className="item">
                      <img src={rmStep1Img} className="image" alt="rewards marketplace step 1" />
                      <h5 className="title">Step 1</h5>
                      <p className="desc">Rewards Marketplace is a rewards playground that gives you full access to exclusive promotions and discounts from our merchants and partners.</p>
                    </div>
                  </Col>

                  <Col xs="12" sm="4">
                    <div className="item">
                      <img src={rmStep2Img} className="image" alt="rewards marketplace step 2" />
                      <h5 className="title">Step 2</h5>
                      <p className="desc">Simply redeem a reward of your choice listed in our Rewards Marketplace. Eg: Use P$3 credits to enjoy $10 off from one of our merchants.</p>
                    </div>
                  </Col>

                  <Col xs="12" sm="4">
                    <div className="item">
                      <img src={rmStep3Img} className="image" alt="rewards marketplace step 2" />
                      <h5 className="title">Step 3</h5>
                      <p className="desc">You can view your redeemed rewards under ‘My Rewards’ section.</p>
                    </div>
                  </Col>
                </Row>
              </div>
            </CarouselItem>
          </Carousel>
        </Col>
      </Row>
    )
  }
}

export default Guides;