import React, { Component } from 'react';
import PropTypes from 'prop-types';
import ReactSlickCarousel from 'components/Templates/ReactSlickCarousel/ReactSlickCarousel';
import {
  omit,
} from 'lodash';
import './SlickCarousel.scss';

const KEYS_EXCEPTION = [
  'images',
  'children',
];

class SlickCarousel extends Component {
  constructor(props) {
    super(props);

    this.getContent = this.getContent.bind(this);
    this.getAttrs = this.getAttrs.bind(this);
    this.renderSlider = this.renderSlider.bind(this);
  }

  getContent() {
    const {
      images,
      children,
    } = this.props;

    if (images && images.length > 0) {
      return images.map((url, idx) => (
        <img src={url} key={idx} alt={idx} />
      ));
    }

    return children;
  }

  getAttrs() {
    let attrs = Object.assign({}, this.props);
    return omit(attrs, KEYS_EXCEPTION);
  }

  renderSlider() {
    // const {
    //   autoplay,
    //   variableWidth,
    //   infinite,
    //   slidesToShow,
    //   slidesToScroll,
    //   className,
    // } = this.props;

    return (
      <ReactSlickCarousel
        lazyLoad
        arrows
        responsive={[
          {
            breakpoint: 600,
            settings: {
              arrows: true,
              slidesToShow: 3,
            },
          },
        ]}
        {...this.getAttrs()}
      >
        { this.getContent() }
      </ReactSlickCarousel>
    )
  }

  render() {
    const {
      images,
      children,
    } = this.props;

    if ((images && images.length > 0) || children) {
      return (
        <div className="slick-carousel">
          {this.renderSlider()}
        </div>
      )
    }
    return null;
  }
}

SlickCarousel.defaultProps = {
  slidesToShow: 4,
  slidesToScroll: 1,
  variableWidth: false,
  autoplay: false,
  infinite: false,
  images: [],
  children: null,
  className: '',
};

SlickCarousel.propTypes = {
  slidesToShow: PropTypes.number,
  slidesToScroll: PropTypes.number,
  variableWidth: PropTypes.bool,
  autoplay: PropTypes.bool,
  infinite: PropTypes.bool,
  images: PropTypes.array,
  children: PropTypes.any,
  className: PropTypes.string,
};

export default SlickCarousel;