import React, { Component } from 'react';
import PropTypes from 'prop-types';

class Title extends Component {
  constructor(props) {
    super(props);

    this.getClasses = this.getClasses.bind(this);
  }

  getClasses() {
    const { className } = this.props;
    const classes  = ['form-landing-information-container'];

    if (className) {
      classes.push(className);
    }

    return classes.join(' ');
  }

  render() {
    const { foregroundText, backgroundText } = this.props;

    return (
      <div className={this.getClasses()}>
        <h2 className="main-title center" dangerouslySetInnerHTML={{ __html: backgroundText || '&nbsp;'  }}/>
        <h2 className="sub-title center" dangerouslySetInnerHTML={{__html: foregroundText || '&nbspl'}}/>
      </div>
    )
  }
}

Title.defaultProps = {
  foregroundText: '',
  backgroundText: '',
  className: '',
};

Title.propTypes = {
  foregroundText: PropTypes.string,
  backgroundText: PropTypes.string,
  className: PropTypes.string,
};

export default Title;
